import React, { Component } from 'react';
import SectionA from '../../elements/components/sectionA/SectionA';
import SectionB from '../../elements/components/sectionB/SectionB';
import MenuContainer from '../../elements/containers/Menu';
import ContentDesign from '../../elements/components/contentDesign/ContentDesign';
import ContentCopyRight from '../../elements/components/contentCopyRight/ContentCopyRight';
import ContentBConfort from '../../elements/components/contentBConfort/ContentBConfort';


class Confort extends Component{
	constructor(props) {
        super(props);
        this.state = {
            step: 1,
            max: 1
        };
    }
    descriptionAndName(){
    	var description = {
    		1: {
    			nameImg: 'Diseño de imagen corporativa',
    			descriptionImg: ''
    		}
    	}

    	return description[this.state.step]
    }
	stepContinue = () =>{
		this.setState({
			step: this.state.step + 1
		})
	}
	stepPrev = () =>{
		this.setState({
			step: this.state.step - 1
		})
	}
	render(){
		return(
			<React.Fragment>
				<SectionA classStyle="landing-section-a-small color-white bg-black-5">
					<MenuContainer handleClick={this.props.handleClick} menuVisible={this.props.menuVisible}></MenuContainer>
					<ContentDesign 
                    title="Leather Confort"
					location="Diseño" 
					step={this.state.step}
					description={this.descriptionAndName()}
					next='/trabajo/diseno/abogados'
                    before='/trabajo/diseno/cook'
					stepContinue={this.stepContinue}
					stepPrev={this.stepPrev}
                    max={this.state.max}
					></ContentDesign>
					<ContentCopyRight></ContentCopyRight>
				</SectionA>
				<SectionB classStyle="landing-section-b">
					<ContentBConfort step={this.state.step}></ContentBConfort>
				</SectionB>
			</React.Fragment>
		)
	}
}

export default Confort