import React, { Component } from 'react';
import SectionA from '../../elements/components/sectionA/SectionA';
import SectionB from '../../elements/components/sectionB/SectionB';
import MenuContainer from '../../elements/containers/Menu';
import ContentIllustration from '../../elements/components/contentIllustration/ContentIllustration';
import ContentCopyRight from '../../elements/components/contentCopyRight/ContentCopyRight';
import ContentBArauca from '../../elements/components/ContentBArauca/ContentBArauca';


class Arauca extends Component{
	constructor(props) {
        super(props);
        this.state = {
            step: 1,
            max: 8
        };
    }
    descriptionAndName(){
    	var description = {
    		1: {
    			nameImg: 'Maicero Cabeciblanco',
    			descriptionImg: 'Cebus albifrons'
    		},
    		2: {
    			nameImg: 'Delfin rosado',
    			descriptionImg: 'Inia geoffrensis'
    		},
    		3: {
    			nameImg: 'Maicero Cabeciblanco',
    			descriptionImg: 'Myrmecophaga tridactiya'
    		},
    		4: {
    			nameImg: 'Nutria',
    			descriptionImg: 'Pteronura brasilensis'
    		},
            5: {
                nameImg: 'Tortuga Morrocoy',
                descriptionImg: 'Chelonoidis carbonara'
            },
    		6: {
    			nameImg: 'Charapa',
    			descriptionImg: 'Podocnemis expansa'
    		},
    		7: {
    			nameImg: 'Lirio Blanco',
    			descriptionImg: 'Cattleya schroederae'
    		},
    		8: {
    			nameImg: 'Cicnoches',
    			descriptionImg: 'Cycnoches dorochilum'
    		}
    	}

    	return description[this.state.step]
    }
	stepContinue = () =>{
		this.setState({
			step: this.state.step + 1
		})
	}
	stepPrev = () =>{
		this.setState({
			step: this.state.step - 1
		})
	}
	render(){
		return(
			<React.Fragment>
				<SectionA classStyle="landing-section-a-small color-white bg-purple-5">
					<MenuContainer handleClick={this.props.handleClick} menuVisible={this.props.menuVisible}></MenuContainer>
					<ContentIllustration 
                    title="Arauca, Reserva de vida"
					location="Ilustración" 
					step={this.state.step}
					description={this.descriptionAndName()}
                    next='/trabajo/ilustracion/flamenco'
                    before='/trabajo/ilustracion/biblored'
					stepContinue={this.stepContinue}
					stepPrev={this.stepPrev}
                    max={this.state.max}
					></ContentIllustration>
					<ContentCopyRight></ContentCopyRight>
				</SectionA>
				<SectionB classStyle="landing-section-b">
					<ContentBArauca step={this.state.step}></ContentBArauca>
				</SectionB>
			</React.Fragment>
		)
	}
}

export default Arauca