import React, { Component } from 'react';
import SectionA from '../../elements/components/sectionA/SectionA';
import SectionB from '../../elements/components/sectionB/SectionB';
import MenuContainer from '../../elements/containers/Menu';
import ContentWorkExhibitions from '../../elements/components/contentWorkExhibitions/ContentWorkExhibitions';
import ContentCopyRight from '../../elements/components/contentCopyRight/ContentCopyRight';
import ContentBSalon from '../../elements/components/ContentBSalon/ContentBSalon';


class Salon extends Component{
	constructor(props) {
        super(props);
        this.state = {
            step: 1,
            max: 3
        };
    }
    descriptionAndName(){
    	var description = {
    		1: {
    			nameImg: 'IUS',
    			descriptionImg: ''
    		},
    		2: {
    			nameImg: 'IUS',
    			descriptionImg: ''
    		},
    		3: {
    			nameImg: 'IUS',
    			descriptionImg: ''
    		}
    	}

    	return description[this.state.step]
    }
	stepContinue = () =>{
		this.setState({
			step: this.state.step + 1
		})
	}
	stepPrev = () =>{
		this.setState({
			step: this.state.step - 1
		})
	}
	render(){
		return(
			<React.Fragment>
				<SectionA classStyle="landing-section-a-small color-white bg-yellow-5">
					<MenuContainer handleClick={this.props.handleClick} menuVisible={this.props.menuVisible}></MenuContainer>
					<ContentWorkExhibitions 
                    title="Salón de la Justicia"
					location="Exposiciones" 
					step={this.state.step}
					description={this.descriptionAndName()}
					next='/trabajo/exposicionesTrabajo/palabra'
                    before='/trabajo/exposicionesTrabajo'
					stepContinue={this.stepContinue}
					stepPrev={this.stepPrev}
                    max={this.state.max}
					></ContentWorkExhibitions>
					<ContentCopyRight></ContentCopyRight>
				</SectionA>
				<SectionB classStyle="landing-section-b">
					<ContentBSalon step={this.state.step}></ContentBSalon>
				</SectionB>
			</React.Fragment>
		)
	}
}

export default Salon