import React, { Component } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import Layout from '../../elements/components/layout/Layout';
import Home from './Home';
import Trabajo from './Trabajo';
import Bio from './Bio';
import Experiencia from './Experiencia';
import Exposiciones from './Exposiciones';
import Ilustracion from './Ilustracion';
import Biblored from './Biblored';
import Arauca from './Arauca';
import Flamenco from './Flamenco';
import Almanaque from './Almanaque';
import Corpo from './Corpo';
import Cine from './Cine';
import Mama from './Mama';
import Danza from './Danza';
import Personal from './Personal';
import ExposicionesTrabajo from './ExposicionesTrabajo';
import Salon from './Salon';
import Palabra from './Palabra';
import Ritmo from './Ritmo';
import Loza from './Loza';
import Nogal from './Nogal';
import Diseno from './Diseno';
import Msfx from './Msfx';
import Cook from './Cook';
import Confort from './Confort';
import Abogados from './Abogados';
import Astronauta from './Astronauta';
import Otros from './Otros';
import Formacion from './Formacion';
import Noticias from './Noticias';
import Contacto from './Contacto';
import NotFound from '../components/NotFound';


class App extends Component{
    constructor(props) {
        super(props);
        this.state = {
            menuVisible: false,
            styleSectionA: " bg-black-5"
        };
    }
    handleClick = () =>{
        if(this.state.menuVisible){
            this.setState({
                menuVisible: false,
                styleSectionA: " bg-black-5"
            })
        }
        else{
            this.setState({
                menuVisible: true,
                styleSectionA: " bg-purple-5"
            })
        }
    }
    componentDidMount(){
        var url = window.location.pathname;

        if(url !== "/" ){
            this.setState({
                menuVisible: true
            })
        }
    }

  render(){
    return (
      <BrowserRouter>
        <Layout>
          <Switch>
            <Route exact path="/" render={() => <Home styleSectionA={this.state.styleSectionA} pepe={this.props.pepe} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo" render={() => <Trabajo styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion" render={() => <Ilustracion styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/biblored" render={() => <Biblored styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/arauca" render={() => <Arauca styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/flamenco" render={() => <Flamenco styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/almanaque" render={() => <Almanaque styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/corpo" render={() => <Corpo styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/cine" render={() => <Cine styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/mama" render={() => <Mama styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/danza" render={() => <Danza styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/ilustracion/personal" render={() => <Personal styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo" render={() => <ExposicionesTrabajo styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo/salon" render={() => <Salon styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo/palabra" render={() => <Palabra styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo/ritmo" render={() => <Ritmo styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo/loza" render={() => <Loza styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/exposicionesTrabajo/nogal" render={() => <Nogal styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno" render={() => <Diseno styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/msfx" render={() => <Msfx styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/cook" render={() => <Cook styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/confort" render={() => <Confort styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/abogados" render={() => <Abogados styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/astronauta" render={() => <Astronauta styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/trabajo/diseno/otros" render={() => <Otros styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/bio" render={() => <Bio styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/bio/experiencia" render={() => <Experiencia styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/bio/exposiciones" render={() => <Exposiciones styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/bio/formacion" render={() => <Formacion styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/noticias" render={() => <Noticias styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route exact path="/contacto" render={() => <Contacto styleSectionA={this.state.styleSectionA} handleClick={this.handleClick} menuVisible={this.state.menuVisible} />} />
            <Route component={NotFound} />
          </Switch>
        </Layout>
      </BrowserRouter>
    );
  }
}

export default App;
