import React from 'react';
import { Link } from 'react-router-dom';
import './Menu.css';
import icon from '../../../images/menu-circle.svg';



function Menu(props){
	return(
		<div className="section-menu">
			<Link to="/">
				<img src={icon} alt="icono menu" onClick={props.handleClick} className="icon-menu cursor" />
			</Link>
			{props.menuVisible &&
				<div className="column menu-options">
					<Link className={props.itemValue === '/trabajo' ? "item-menu color-white active-yellow" : "item-menu color-white"} to="/trabajo">Trabajo</Link>
					{props.subitemVisibleWork &&
						<React.Fragment>
							<Link className={(props.itemValue.includes('/trabajo/ilustracion')) ? "sub-item-menu color-white active-yellow" : "sub-item-menu color-white"} to="/trabajo/ilustracion">Ilustración</Link>
							<Link className={(props.itemValue.includes('/trabajo/exposicionesTrabajo')) ? "sub-item-menu color-white active-purple" : "sub-item-menu color-white"} to="/trabajo/exposicionesTrabajo">Exposiciones</Link>
							<Link className={(props.itemValue.includes('/trabajo/diseno')) ? "sub-item-menu color-white active-yellow" : "sub-item-menu color-white"} to="/trabajo/diseno">Diseño</Link>
						</React.Fragment>
					}
					<Link className={props.itemValue === '/bio' ? "item-menu color-white active-purple" : "item-menu color-white"} to="/bio">Bio</Link>
					{props.subitemVisible &&
						<React.Fragment>
							<Link className={props.itemValue === '/bio/experiencia' ? "sub-item-menu color-white active-purple" : "sub-item-menu color-white"} to="/bio/experiencia">Experiencia</Link>
							<Link className={props.itemValue === '/bio/exposiciones' ? "sub-item-menu color-white active-purple" : "sub-item-menu color-white"} to="/bio/exposiciones">Exposiciones</Link>
							<Link className={props.itemValue === '/bio/formacion' ? "sub-item-menu color-white active-purple" : "sub-item-menu color-white"} to="/bio/formacion">Formación</Link>
						</React.Fragment>
					}
					<Link className={props.itemValue === '/noticias' ? "item-menu color-white active-white" : "item-menu color-white"} to="/noticias">Noticias</Link>
					<Link className={props.itemValue === '/contacto' ? "item-menu color-white active-yellow" : "item-menu color-white"} to="/contacto">Contacto</Link>
				</div>
			}
		</div>
	)
}

export default Menu