import React, { Component } from 'react';
import Menu from '../components/menu/Menu.js'

class MenuContainer extends Component{
	constructor(props) {
        super(props);
        this.state = {
            itemValue: '',
            subitemVisible: false,
            subitemVisibleWork: false
        };
    }
    componentDidMount(){
    	this.activeItemMenu()
    }
	activeItemMenu = () =>{
        var url = window.location.pathname;

    	this.setState({
            itemValue: url,
            subitemVisible: url.includes('/bio') ? true : false,
            subitemVisibleWork: url.includes('/trabajo') ? true : false
        })
        
    }


	render(){
		return(
			<Menu handleClick={this.props.handleClick} menuVisible={this.props.menuVisible} itemValue={this.state.itemValue} subitemVisible={this.state.subitemVisible} subitemVisibleWork={this.state.subitemVisibleWork} >
			</Menu>
		)
	}

}

export default MenuContainer