import React, { Component } from 'react';
import SectionA from '../../elements/components/sectionA/SectionA';
import SectionB from '../../elements/components/sectionB/SectionB';
import MenuContainer from '../../elements/containers/Menu';
import ContentIllustration from '../../elements/components/contentIllustration/ContentIllustration';
import ContentCopyRight from '../../elements/components/contentCopyRight/ContentCopyRight';
import ContentBPersonal from '../../elements/components/ContentBPersonal/ContentBPersonal';


class Personal extends Component{
	constructor(props) {
        super(props);
        this.state = {
            step: 1,
            max: 14
        };
    }
    descriptionAndName(){
    	var description = {
    		1: {
    			nameImg: 'Sin título',
    			descriptionImg: ''
    		},
    		2: {
    			nameImg: 'Diseño de pañoleta',
    			descriptionImg: ''
    		},
    		3: {
    			nameImg: 'Teatro',
    			descriptionImg: ''
    		},
    		4: {
    			nameImg: 'Proyecto Manatí Antillano',
    			descriptionImg: ''
    		},
            5: {
                nameImg: 'Nostalgia',
                descriptionImg: ''
            },
    		6: {
    			nameImg: 'Angulus virginales',
    			descriptionImg: ''
    		},
    		7: {
    			nameImg: 'Hombre jaguar',
    			descriptionImg: ''
    		},
    		8: {
                nameImg: 'Pierrot',
                descriptionImg: ''
            },
            9: {
                nameImg: 'El gato bandido',
                descriptionImg: ''
            },
            10: {
                nameImg: 'Trogones',
                descriptionImg: ''
            },
            11: {
                nameImg: 'Eubucco bourcierii',
                descriptionImg: ''
            },
            12: {
                nameImg: 'Cardenal',
                descriptionImg: ''
            },
            13: {
                nameImg: 'Cóndor',
                descriptionImg: ''
            },
            14: {
    			nameImg: 'Azulejo muerto',
    			descriptionImg: ''
    		}
    	}

    	return description[this.state.step]
    }
	stepContinue = () =>{
		this.setState({
			step: this.state.step + 1
		})
	}
	stepPrev = () =>{
		this.setState({
			step: this.state.step - 1
		})
	}
	render(){
		return(
			<React.Fragment>
				<SectionA classStyle="landing-section-a-small color-white bg-purple-5">
					<MenuContainer handleClick={this.props.handleClick} menuVisible={this.props.menuVisible}></MenuContainer>
					<ContentIllustration 
                    title="Personal"
					location="Ilustración" 
					step={this.state.step}
					description={this.descriptionAndName()}
                    next='/trabajo/exposicionesTrabajo'
                    before='/trabajo/ilustracion/danza'
					stepContinue={this.stepContinue}
					stepPrev={this.stepPrev}
                    max={this.state.max}
					></ContentIllustration>
					<ContentCopyRight></ContentCopyRight>
				</SectionA>
				<SectionB classStyle="landing-section-b">
					<ContentBPersonal step={this.state.step}></ContentBPersonal>
				</SectionB>
			</React.Fragment>
		)
	}
}

export default Personal